export default "curl 'http://en.wikipedia.org/' \\\n    -H 'Accept-Encoding: gzip, deflate, sdch' \\\n    -H 'Accept-Language: en-US,en;q=0.8' \\\n    -H 'User-Agent: {{useragent}}' \\\n    -H 'Accept: text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,*/*;q=0.8' \\\n    -H 'Referer: http://www.wikipedia.org/' \\\n    -H 'Connection: keep-alive' --compressed\n";